.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: black;
  font-family: 'Bangers', cursive;
}

h1, p {
  color: red;
  text-shadow: 2px 0 2px #fff, -2px 0 2px #fff, 0 -2px 2px #fff, 0 2px 2px #fff;
}

h1 {
  font-size: 50px;
}

p {
  font-size: 40px;
}

div {
  margin-top: 0;
  background-color: rgba(0, 0, 0, .4);
}

.marvel-logo {
  width: 70%;
  height: auto;
}

img {
  border: 10px solid white;
  width: 300px;
  height: auto;
}

nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 30px;
  border: 2px solid white;
}

a {
  flex-direction: row;
  padding: 20px;
  color: white;
  text-decoration: none;
  list-style: none;
  font-size: 65px;
}

li {
  color: white;
  list-style: none;
}

li:hover {
  color: #e0c541;
}

li:active {
  color: red;
  text-shadow: 2px 0 2px #fff, -2px 0 2px #fff, 0 -2px 2px #fff, 0 2px 2px #fff;
}

h2 {
  color: white;
}

.event {
  display: flex;
  border: 2px solid white;
  margin: 30px 0;
  padding: 10px;
  width: 100%;
}

.event > img {
  width: 300px;
  height: 90%;
  margin: auto;
}

.event-desc {
  margin: 10px;
}

.event-desc > h1 {
  font-size: 60px;
}

.character-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
}

.character-list > li {
  width: 15%;
}

.all-comic-div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
}

.comic {
  width: 20%;
  height: 500px;
  border-radius: 5px;
  padding: 0;
}

@media screen and (max-width: 540px) {
  .comic {
    width: 90%;
    height: auto;
    border-radius: 5px;
    padding: 0;
  }
}

.comic:hover {
  background-color: #e0c541;
}

.comic:active {
  background-color: white;
}

.comic > img {
  width: 80%;
  height: auto;
  margin: auto;
}

.comic-desc > h1 {
  font-size: 40px;
  padding: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.comic-button {
  font-family: 'Bangers', cursive;
  font-size: 30px;
  color: white;
  background-color: red;
  float: right;
  margin: 70px 30px;
  width: 100px;
}

.comic-button:active {
  color: red;
  background-color: white;
}

.comic-button:focus {
  outline:0;
}

.back {
  float: left;
}

label > input {
  font-family: 'Bangers', cursive;
  color: black;
  font-size: 30px;
  margin: 20px 10px;
  text-align: center;
}

input:focus {
  outline: 0;
}

.single-comic-div {
  margin: 90px 40px;
}

@media screen and (max-width: 540px) {
  .single-comic-div {
    margin: 0 auto;
  }
}

.single-comic-div > img {
  float: left;
}

@media screen and (max-width: 540px) {
  .single-comic-div > img {
    float: none;
    width: 80%;
    height: auto;
  }
}

.single-comic-desc {
  float: right;
  width: 70%;
  height: 100%;
}

@media screen and (max-width: 540px) {
  .single-comic-desc {
    float: none;
    width: 85%;
    margin: 0 auto;
    font-size: 30px;
    line-height: 1.2;
  }
}

.single-comic-desc > h1 {
  font-size: 50px;
}

.single-comic-desc > p {
  font-size: 30px; 
}